.head{
    color: brown;
}

.home{
    width: 50%;
    top : 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
}


/* Quote */

.quote{
    color: #464F41;
    padding: 10px;
    border: 5px solid white ;
    background-color: #BFCBA8;
    height: 100px;
    margin: 20px 5px;
    justify-items: center;
    font-size: large;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
}

.heading{
    color: #BFCBA8;
    margin: 20px 5px;
}

.quote-box{
    top : 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    background-color:#56776C;
    padding : 50px;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    width: 50%;
}

.quotebutton{
    background-color: beige;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

/* .quotebutton:hover{
    
} */

.quotebutton:active{
    opacity: 0.7;
    transform: scale(.9);
}


/* Accordian */

.title {
    background-color: #836f5f;
    color: #fffeff !important;
    width: 100%;
    height: 50px;
    margin-top: 20px;
    opacity: 0.9;
}

.main {
    margin-top: 10px;
    justify-content: center;
    background-color: #D7B8A3; /* Light Brown */
    /* opacity: 0.95; */
    color: black;
    padding: 10px;
    opacity: 0.65;
}

.accbox {
    width: 50%;
    background-color: #FCE8D5; /* Light Orange */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    padding: 30px;
}

